.card {
  height: 250px;
  margin: 1rem;
  position: relative;
  width: 250px;
  box-shadow: 0 3px 6px #333, 0 3px 6px #333;
  border-radius: 100%;
}

.card > .img-container {
  height: 100%;
  overflow: hidden;
  text-align: center;
  border-radius: 100%;
}

.card > .img-container > img {
  width: 100%;
  border-radius: 100%;
  border: 1px solid #fff;
}

.wrapper {
  padding-top: 50px;
  display: flex;
  flex-flow: row wrap;
  padding: 20px;
  justify-content: space-around;
  align-content: flex-start;
  max-width: 1240px;
  margin: 0 auto 20px auto;
}

.header{
  width: 100%;
  text-align: center;
  font-size: 3rem;
  color: #fff;
  margin-bottom: 50px;
  line-height: 1;
}

h1 {
  margin-top: 1rem;
  margin-bottom: 0;
  padding-bottom: 1rem;
}

h4 {
  margin: 0;
}

.scores {
  margin: 0 auto;
  width: 40%;
}

p {
  font-size: 1.5rem;
  width: 50%;
  display: inline-block;
  margin: 0;
}
* {
  box-sizing: border-box;
  font-family: sans-serif;
}

html, body, #root {
  padding: 0;
  margin: 0;
  height: 100%;
  background-image: url('https://github.com/zcdev/clicky-owls/blob/master/public/images/forest.jpg?raw=true');
  background-attachment: fixed;
  background-size: cover;
  background-repeat: no-repeat;
}

